@import url(https://fonts.googleapis.com/css2?family=Jolly+Lodger&display=swap);





.form {
    background-image: url(/static/media/bloodbackground.9a7ec444.jpeg);
    background-repeat: no-repeat;
    height: 100vh;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    }
    .title{
        font-size: 4em;
    }
    .font {
        color: #ffff;
        font-size: 2em;
    }
    .button {
        color: rgb(192, 3, 3);
        font-size: 1.5em;
        font-weight: bold;
        background-color: #ffff;
        border-radius: 25px;
        border-color: #ffff;
        margin-left: -25%;
        display: inline-block;
        height: 50px;
    }
    .button:hover{
        background-color:rgb(211, 5, 5);
        border-color: #ffff;
    }
    .input {
        border-radius: 15px;
        width: 130%;
        height: 35px;
    }
    .inputmargin {
        margin-left: 10%;
    }
    .inputtext{
        margin-left: 35%;
    }
    
.bar{
    background-color: black;
    height: 30px;
}

.footer{
    background-color: black;
    height: 100px;
}

h1{
    color:white;
    font-family: 'Jolly Lodger', cursive;
    text-align: center;
}

p{
    color:white;
    font-family: 'Jolly Lodger', cursive;
    text-align: center;
    font-size: 25px; 
}





.footer{
    font-size: 1.2em;
    background-color: black;
    height: 10%;
    display: flex;
    flex-direction: column;

}

.links {
    color: white;
}
.dad{
    width: 100vw;
    height: 100vh;
    background-color: gray;
    display: grid;
    place-items:center ;
}

.wrapper{
    background-color: lightgray;
    padding: 100px;
    border-radius: 10px;
}
